.hero-section {
  background: linear-gradient(hsl(252, 100%, 67%), hsl(241, 81%, 54%));
}
.hero-section h1, .hero-section p {
  color: hsl(241, 100%, 89%);
}
.hero-section h2 {
  background: linear-gradient(hsl(256, 72%, 46%), hsla(241, 72%, 46%, 0));
}
.hero-section h2 span {
  color: hsla(241, 100%, 89%, 0.4);
}
.hero-section h2, .hero-section h3 {
  color: hsl(0, 0%, 100%);
}

.summary-section article {
  background-color: hsl(221, 100%, 96%);
}
.summary-section button {
  transition: background 0.6s ease;
}
.summary-section button:hover {
  background: linear-gradient(hsl(252, 100%, 67%), hsl(241, 81%, 54%));
  cursor: pointer;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  position: relative;
  display: grid;
  place-items: center;
}

main {
  min-height: 100vh;
  text-align: center;
  animation: introLoad 1s forwards;
}
main .hero-section {
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
}

footer {
  position: absolute;
  bottom: 0;
}

@media only screen and (min-width: 700px) {
  main {
    max-width: 38.8888888889rem;
    min-height: 24.1666666667rem;
    width: 80%;
    border-radius: 2rem;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(2, minmax(auto, 19.4444444444rem));
    box-shadow: 5px 4px 15px 2px rgba(0, 0, 0, 0.2);
  }
  main .hero-section {
    border-top-right-radius: 2rem;
  }
  @keyframes introLoad {
    0% {
      transform: translateY(-10rem);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
@font-face {
  font-family: "hankengrotesk";
  src: url(../assets/fonts/hankengrotesk-variablefont_wght-webfont.woff) format("woff"), url(../assets/fonts/hankengrotesk-variablefont_wght-webfont.woff2) format("woff2");
  font-weight: normal;
  font-style: normal;
}
html {
  font-size: 18px;
  font-family: "hankengrotesk", "Hanken Grotesk", sans-serif;
}

.hero-section {
  padding: 1.3rem 3rem 2rem 3rem;
  display: flex;
  flex-direction: column;
}
.hero-section h1 {
  font-size: 0.9rem;
  font-weight: 700;
  margin-bottom: 1.3rem;
}
.hero-section h2 {
  padding: 1.4rem 0;
  align-self: center;
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1.3rem;
  min-width: 7.4138888889rem;
  min-height: 7.1444444444rem;
  border-radius: 50%;
}
.hero-section h2 span {
  font-size: 0.8rem;
  display: block;
}
.hero-section h3 {
  margin-bottom: 0.4rem;
}
.hero-section p {
  font-size: 0.8rem;
}

.summary-section {
  padding: 1.5rem 1.6rem;
}
.summary-section h3 {
  text-align: left;
  font-size: 1rem;
  margin-bottom: 1.5rem;
  color: hsl(224, 30%, 27%);
}
.summary-section article {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 0.7rem;
  border-radius: 0.6666666667rem;
  margin-bottom: 1rem;
}
.summary-section article h4:nth-child(1) {
  font-weight: 700;
  font-size: 0.9rem;
}
.summary-section article h4:nth-child(1) img {
  margin-bottom: -0.2222222222rem;
  margin-right: 0.3888888889rem;
}
.summary-section article h4:nth-child(2) {
  color: hsl(241, 100%, 89%);
  font-size: 0.9rem;
  font-weight: 700;
}
.summary-section article h4:nth-child(2) strong {
  color: hsl(224, 30%, 27%);
}
.summary-section article:nth-of-type(1) {
  background-color: hsla(0, 100%, 67%, 0.1);
}
.summary-section article:nth-of-type(1) h4:nth-child(1) {
  color: hsl(0, 100%, 67%);
}
.summary-section article:nth-of-type(2) {
  background-color: hsla(39, 100%, 56%, 0.1);
}
.summary-section article:nth-of-type(2) h4:nth-child(1) {
  color: hsl(39, 100%, 56%);
}
.summary-section article:nth-of-type(3) {
  background-color: hsla(166, 100%, 37%, 0.1);
}
.summary-section article:nth-of-type(3) h4:nth-child(1) {
  color: hsl(166, 100%, 37%);
}
.summary-section article:nth-of-type(4) {
  background-color: hsla(234, 85%, 45%, 0.1);
  margin-bottom: 1.6rem;
}
.summary-section article:nth-of-type(4) h4:nth-child(1) {
  color: hsl(234, 85%, 45%);
}
.summary-section button {
  width: 100%;
  font-family: inherit;
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  background-color: hsl(224, 30%, 27%);
  color: hsl(0, 0%, 100%);
  border-radius: 2rem;
  padding: 0.8rem 0;
}

footer {
  display: none;
}

.attribution {
  font-size: 0.6111111111rem;
  text-align: center;
}

@media only screen and (min-width: 700px) {
  .hero-section {
    padding: 2rem 4rem;
  }
  .hero-section h1 {
    margin-bottom: 1.5rem;
  }
  .hero-section h2 {
    margin-bottom: 1.5rem;
  }
  .hero-section h3 {
    margin-bottom: 1.1rem;
  }
  .hero-section p {
    line-height: 1.5;
  }
  .summary-section {
    padding: 2rem 2rem;
  }
  footer {
    display: block;
  }
}
@media only screen and (min-width: 1200px) {
  html {
    font-size: 18.5px;
  }
}
@media only screen and (min-width: 1400px) {
  html {
    font-size: 19px;
  }
}